"use client";

import { useState } from "react";
import Link from "next/link";
import { useCart } from "@/context/CartContext";

export default function NavBar() {
  const { getCartItemsCount } = useCart();
  const totalItems = getCartItemsCount();
  const [menuOpen, setMenuOpen] = useState(false);

  const toggleMenu = () => {
    setMenuOpen(!menuOpen);
  };

  return (
    <nav className="bg-white shadow-lg fixed w-full z-50">
      <div className="max-w-7xl mx-auto px-4">
        <div className="flex justify-between h-16">
          <div className="flex items-center">
            <Link href="/" className="text-2xl font-bold text-gray-800">
              Apparel Store
            </Link>
          </div>
          {/* Desktop Menu */}
          <div className="hidden md:flex items-center space-x-8">
            <Link href="/" className="text-gray-600 hover:text-gray-900 transition-colors">
              Home
            </Link>
            <Link href="/about" className="text-gray-600 hover:text-gray-900 transition-colors">
              About
            </Link>
            <Link href="/feedback" className="text-gray-600 hover:text-gray-900 transition-colors">
              Feedback
            </Link>
            <Link
              href="/checkout"
              className="flex items-center space-x-2 text-gray-600 hover:text-gray-900 transition-colors relative"
            >
              <span>Cart</span>
              {totalItems > 0 && (
                <span className="absolute -top-2 -right-2 bg-black text-white w-5 h-5 flex items-center justify-center rounded-full text-xs">
                  {totalItems}
                </span>
              )}
            </Link>
          </div>

          {/* Mobile Menu Toggle */}
          <div className="flex md:hidden items-center">
            <button onClick={toggleMenu} aria-label="Toggle menu">
              {/* Simple Hamburger Icon */}
              <svg
                className="w-6 h-6 text-gray-800"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
                xmlns="http://www.w3.org/2000/svg"
              >
                {menuOpen ? (
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M6 18L18 6M6 6l12 12"
                  />
                ) : (
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 8h16M4 16h16" />
                )}
              </svg>
            </button>
          </div>
        </div>
      </div>

      {/* Mobile Dropdown Menu */}
      {menuOpen && (
        <div className="md:hidden bg-white shadow-md">
          <div className="px-4 pt-2 pb-4 space-y-2">
            <Link href="/" className="block text-gray-600 hover:text-gray-900 transition-colors" onClick={() => setMenuOpen(false)}>
              Home
            </Link>
            <Link href="/about" className="block text-gray-600 hover:text-gray-900 transition-colors" onClick={() => setMenuOpen(false)}>
              About
            </Link>
            <Link href="/feedback" className="block text-gray-600 hover:text-gray-900 transition-colors" onClick={() => setMenuOpen(false)}>
              Feedback
            </Link>
            <Link
              href="/checkout"
              className="block text-gray-600 hover:text-gray-900 transition-colors relative"
              onClick={() => setMenuOpen(false)}
            >
              <span>Cart</span>
              {totalItems > 0 && (
                <span className="absolute top-0 right-0 bg-black text-white w-5 h-5 flex items-center justify-center rounded-full text-xs">
                  {totalItems}
                </span>
              )}
            </Link>
          </div>
        </div>
      )}
    </nav>
  );
}
